import React from 'react';
import { connect } from 'react-redux'

import Input from '../../UI/Input/Input';
import * as actions from '../../storage/actions/actions'

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            controls: {
                username: {
                    id: 'username',
                    type: 'username',
                    label: 'Username',
                    value: null,
                    placeholder: 'Username'
                },
                password: {
                    id: 'password',
                    type: 'password',
                    label: 'Password',
                    value: null,
                    placeholder: 'Enter password'
                }
            }
        }
    }

    onInputChanged = (event, key) => {
        const element = event.target;
        const control = { ...this.state.controls[key] }
        control.value = element.value;
        const controlsUpdated = {
            ...this.state.controls,
            [key]: control
        };
        this.setState({ controls : controlsUpdated });
    }

    onLogin = () => {
        const username = this.state.controls.username.value;
        const password = this.state.controls.password.value;
         this.props.login(username, password);
    }

    render() {
        console.log(this.state.controls);
        const formElemets = [];
        Object.keys(this.state.controls).forEach(key => {
            const input = this.state.controls[key];
            formElemets.push(<Input key={key} {...input} changed={(event) => this.onInputChanged(event, key)} />)
        });

        return (
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-5">
                        <form>
                            {formElemets}
                            <button type="button" onClick={this.onLogin}>Login</button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.auth.isAuthenticated
    }
}

const mapDispatchToState = (dispatch) => {
    return {
        login: (username, passport) => dispatch(actions.login(username, passport))
    }
}

export default connect(mapStateToProps, mapDispatchToState)(Login);